.bg-blue {
    background-color: #2d354c;
}

.carousel-card {
    background-color: #ffffff;
    border-radius: 6px;
    box-sizing: border-box;
    box-shadow: 2px 4px 12px rgb(0 0 0 / 12%);
    flex-wrap: wrap;
    padding: 24px 32px 32px;
}

.room-row-datails {
    padding: 4em 0em 6em;
    justify-content: center;
    align-items: center;
}

.room-datails-card {
    padding: 0 0 0 3em;
    color: #b5c1e5;
}

.room-datails-card h1 {
    color: #ffffff;
    text-align: left;
    border-left: 3px solid #ffffff;
    padding: .5em;
    margin-bottom: 2rem;
}

.room-datails-card p {
    margin-bottom: 2em;
}

.room-details-icons {
    display: flex;
    text-align: center;
    padding: 2em 1em 1em;
    justify-content: space-between;
    align-items: center;
    border-top: 4px solid #ffffff;
}

.room-details-icons .icon-font {
    font-size: 30px;
}

.datails-box p {
    margin-top: .8em;
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
}

.btn-roomd {
    display: inline-block;
    margin-top: 1.6rem;
    text-align: center;
    border-radius: 0.4rem;
    font-size: 1.3em;
    font-weight: 600;
    letter-spacing: .09em;
    width: 100%;
    background-color: #ffffff;
    border-color: #ffffff;
    padding: .8rem 0;
    color: #2d354c;
    transition: all .5s ease 0s;
}

.btn-roomd a {
    color: #ffffff;
}

.btn-roomd:hover {
    box-shadow: 2px 4px 12px rgb(33, 124, 183 /12%);
    border: 2px solid #2dc5fa;
    color: #ffffff;
    background-color: #2dc5fa;
}

.btn-roomd a:hover {
    color: #ffffff;
}

/* QUERIES */

@media (max-width: 767px) {
    .room-datails-card {
        padding: 3em 1em 0;
    }
    .room-row-datails {
        padding: 2em 0 4em;
    }
    .carousel-card {
        padding: .9em;
    }
}