.form-menu h1 {
    color: #ffffff;
    text-align: left;
    border-left: 3px solid #ffffff;
    padding: .5em;
    margin-bottom: 2rem;
}

.row-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0em 8em 3em;
}

.option-menu {
    width: 80%;
    display: flex;
    flex-direction: column;
    padding: 3em;
    flex-wrap: wrap;
    margin: 4em auto;
}

.option-menu h1 {
    color: #ffffff;
    text-align: left;
    border-left: 3px solid #ffffff;
    padding: .5em;
    margin-bottom: 2rem;
}

.option-menu input[type=number], input[type=date] {
    border: 2px solid #e1e8ee;
    border-radius: 6px;
    padding: 8px 6px;
    margin-bottom: .5rem;
    color: #2dc5fa;
    font-family: sans-serif;
}

.option-menu label {
    font-size: 1.8em;
    color: #b5c1e5;
    padding: 8px 0;
    margin-bottom: .5rem;
}

.btn-box-menu {
    margin-top: 20px;
    width: 100%;
    background-color: #2dc5fa;
    border-color: #2dc5fa;
    padding: .5em;
    font-size: 1.3em;
    font-weight: 500;
    letter-spacing: .09em;
}

.btn-box-menu:disabled {
    color: #2D354C;
    background-color: #a9e8ff;
    border-color: #a9e8ff;
}

.btn-box-menu:hover {
    color: #2D354C;
    background-color: #ffffff;
    border-color: #ffffff;
}

.ticket-menu {
    width: 25em;
    display: flex;
    flex-direction: column;
    padding: 3em;
    background: #fff;
    border-radius: 6px;
    box-sizing: border-box;
    box-shadow: 2px 4px 12px rgb(45 53 76 / 50%);
    flex-wrap: wrap;
    margin: 4em auto;
}

.ticket-title-menu {
    display: flex;
    justify-content: space-between;
}

.logo-ticket-menu {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    width: 100%;
}

.ticket-menu h2 {
    color: #2D354C;
    text-align: left;
    border-left: 3px solid #2D354C;
    padding: .5em;
    margin-bottom: 2rem;
}

.ticket-menu h3 {
    color: #2D354C;
    font-weight: 600;
    letter-spacing: .09em;
    width: 80%;
    padding-top: .6em;
    border-top: .1em solid #2dc5fa;
}

.logo-ticket-menu {
    width: 20%;
    object-fit: contain;
}

.ticket-body-menu {
    margin-top: 2em;
    color: #2D354C;
}

.ticket-body-menu strong {
    font-size: 1.08em;
    font-weight: 600;
    letter-spacing: .09em;
}

.form-contain {
    padding: 4em 8em 5em;
}

.form-contain h1 {
    color: #2D354C;
    text-align: left;
    border-left: 3px solid #2D354C;
    padding: .5em;
    margin-bottom: 2rem;
}

.form-contain label {
    font-size: 1.8em;
    color: #2D354C;
    padding: 1em 0;
    margin-bottom: .5rem;
}

.form-check-input {
    width: 2em;
    height: 2em;
    margin-top: .25em;
    vertical-align: top;
    background-color: #2D354C;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    border: 2px solid #e1e8ee;
}

.form-check-input:checked {
    background-color: #2dc5fa;
    border-color: #2dc5fa;
}

/* QUERIES */

@media (max-width: 768px) {
    .row-menu {
        padding: 0;
    }
    .option-menu {
        padding: 0;
    }
    .ticket-menu {
        width: 100%;
    }
    .form-contain {
        padding: 4em 2em;
    }
    .food-card {
        max-width: 100%;
        margin: .8em;
    }
    .food-details-modal {
        padding: 0;
    }
    .details-card-menu {
        padding: 3em 1em 0;
    }
    .carousel-card-menu {
        padding: 1em;
    }
    .details-icons-menu {
        padding: 0;
        font-size: .8em;
        margin-top: 2em;
    }
}